.dogo-bg-black{
  background-color: black;
}

@keyframes float {
  0% {
    transform: translatey(0px);
  }
  50% {
    transform: translatey(-40px);
  }
  100% {
    transform: translatey(0px);
  }
}

.animation {
  animation: float 6s ease-in-out infinite;
}

.dogo-align-owl .owl-item{
  display: flex;
  justify-content: center;
  align-items: center;
}

#typed-strings {
  font-size: 1.5rem;
}